.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  padding: 0 16px;
  display: flex;
}

.card_img {
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-bottom: 40px;
}

.card__product-name {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  align-self: flex-start;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  line-height: 18px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
}

.card__buy-button {
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 16px 0;
  font-size: 14px;
  font-weight: 700;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.stars__items {
  gap: 4px;
  display: flex;
}

.stars__items-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  justify-content: center;
  width: 16px;
  height: 16px;
}

.stars__items-star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__review-title {
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

body {
  margin: 0;
}
